body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }
  code .test {
    color: aqua; }

.link {
  text-decoration: none;
  font-weight: normal;
  font-style: normal; }

h1, h2, h3, h4, h5, h6 {
  color: #2D2D2D; }

.App {
  text-align: center; }
  .App .App-header {
    background-color: #F2F2F2;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white; }
    .App .App-header .App-logo {
      -webkit-animation: App-logo-spin infinite 20s linear;
              animation: App-logo-spin infinite 20s linear;
      height: 40vmin; }

.App-link {
  color: #61dafb;
  margin: 30px auto; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.section .section_title {
  font-size: 2em;
  text-align: center;
  color: #FFFFFF;
  font-weight: 700; }

.section.section_one {
  background: #00F260;
  background: linear-gradient(to right, #0575E6, #00F260); }
  .section.section_one .enter_anchor {
    text-decoration: none;
    color: #FFFFFF; }
    .section.section_one .enter_anchor .enter_btn {
      font-size: .75em;
      background: #FDFC47;
      background: linear-gradient(to right, #24FE41, #FDFC47);
      color: #000000;
      text-shadow: 1px 1px 5px rgba(0, 0, 0, 0.3);
      padding: .5em 2.5em;
      transition: all .3s ease-in-out; }
      .section.section_one .enter_anchor .enter_btn:hover {
        opacity: .75; }

.section.section_two {
  background: #00c6ff;
  /* fallback for old browsers */
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #0072ff, #00c6ff);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */ }
  .section.section_two .section_two_title {
    margin: 10px auto;
    text-align: left;
    letter-spacing: .5em;
    color: #FFFFFF; }

.section.section_three {
  background: #12c2e9;
  /* fallback for old browsers */
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #f64f59, #c471ed, #12c2e9);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */ }

.fp-section {
  text-align: center; }

